import React, { useEffect, useState } from "react";
import "./TestDetails.scss";
import StepInfo from "../../component/stepInfo/StepInfo";
import { Outlet, useParams } from "react-router-dom";
import { getTestDetails } from "../../Redux/TestDetailReducer/TestDetailSlice";
import { useDispatch, useSelector } from "react-redux";
import { NavLink, useNavigate } from "react-router-dom";
import { MdPriorityHigh } from "react-icons/md";
import { BsArrowLeftShort } from "react-icons/bs";
import { BiEdit } from "react-icons/bi";
import { CiUser } from "react-icons/ci";
import axiosWithAuth from "../../utils/axiosClienttok";

function TestDetails() {
  const [enableEdit, setEnableEdit] = useState(true);
  const [status, setStatus] = useState("");
  const [automationStatus, setAutomationStatus] = useState("");
  const [Priorities, setPriority] = useState("");

  const params = useParams();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const match = params.id.match(/\d+/)[0];

  const Priority = 1;

  console.log(match);

  const testDetail = useSelector((state) => state.TestDetailReducer.testDetail);

  //  run on initial render and get the tesst detail  from redux
  useEffect(() => {
    dispatch(getTestDetails(match));
  }, []);

  async function updateTestInfo() {
    var statusP = testDetail?.status;
    var automationStatusP = testDetail?.automation_status;
    var priorityP = testDetail?.priority;

    if (status !== "") {
      statusP = status;
    }
    if (automationStatus !== "") {
      automationStatusP = automationStatus;
    }
    if (Priorities !== "") {
      priorityP = Priorities;
    }
    try {
      const response = await axiosWithAuth.patch(`/test-case/${match}/`, {
        status: statusP,
        automation_status: automationStatusP,
        priority: priorityP,
      });
      console.log(response);
    } catch (error) {
      console.log(error);
    } finally {
      dispatch(getTestDetails(match));
      setEnableEdit(true);
      setAutomationStatus("");
      setPriority("");
      setStatus("");
    }
  }
  const handleStatusChange = (e) => {
    setStatus(e.target.value);
  };

  const handleAutomationStatusChange = (e) => {
    setAutomationStatus(e.target.value);
  };

  const handlePriorityChange = (e) => {
    setPriority(e.target.value);
  };

  const formatClassName = (className) => {
    switch (className) {
      case "class_1":
        return "Class 1";
      case "class_2":
        return "Class 2";
      case "class_3":
        return "Class 3";
      default:
        return className;
    }
  };

  const formatAutomationStatus = (atStatus) => {
    switch (atStatus) {
      case "not-automatable":
        return "Not-Automatable";
      case "automatabel":
        return "Automatable";
      case "in-development":
        return "In-Development";
      case "review":
        return "Review";
      case "ready":
        return "Ready";
      default:
        return atStatus;
    }
  };

  const formatStatus = (atStatus) => {
    switch (atStatus) {
      case "todo":
        return "To-Do";
      case "ongoing":
        return "On-Going";
      case "completed":
        return "Completed";
      default:
        return atStatus;
    }
  };

  return (
    <div className="testDetail_page">
      <div className="test_detail">
        <div className="inner_testdetail">
          <div className="test_case_name-and-info">
            {enableEdit === false && (
              <button
                onClick={updateTestInfo}
                style={{
                  position: "absolute",
                  right: "10px",
                  bottom: "10px",
                  padding: "2px 5px",
                  border: "none",
                  backgroundColor: "#2f63c7",
                  color: "white",
                  borderRadius: "3px",
                  fontSize: "0.8vw",
                  cursor: "pointer",
                }}
              >
                Save
              </button>
            )}
            <div className="testcase-name-status">
              <div className="navigate-edit">
                <p onClick={() => navigate("/")}>
                  <BsArrowLeftShort style={{ fontSize: "1rem" }} /> Back
                </p>
                <p onClick={() => setEnableEdit(!enableEdit)}>
                  <BiEdit style={{ fontSize: "1vw" }} />
                </p>
              </div>
              <h2>
                {testDetail?.id} : {testDetail?.test_name}
              </h2>

              <div className="st">
                <p
                  style={{ width: "40%", color: "#5d7290",  }}
                >
                  Status
                </p>
                {enableEdit ? (
                  <p
                    style={{
                      backgroundColor: "#ebf0f5",
                      fontSize: "0.8rem",
                      padding: "2px 6px",
                      border: "1.5px solid #d7dee7",
                      borderRadius: "4px",
                      color: "#455365",
                    }}
                  >
                    {formatStatus(testDetail?.status)}
                  </p>
                ) : (
                  <div className="form-group-drop">
                    {/* <label htmlFor="priority" style={{ marginRight: "10px" }}>
                  Status:
                </label> */}
                    <select
                      id="status"
                      name="status"
                      value={status}
                      onChange={handleStatusChange}
                      style={{
                        outline: "none",
                        border: "1.5px solid #d7dee7",
                        padding: "4px 5px",
                        borderRadius: "4px",
                        transition: "border-color 0.3s, box-shadow 0.3s",
                        fontSize: "0.7rem",
                        color: "#525b6c",
                        fontWeight: "500",
                        backgroundColor: "#ebf0f5",
                      }}
                      onFocus={(e) => {
                        e.target.style.border = "1.5px solid #d7dee7";
                        e.target.style.boxShadow = "0 0 2px #d7dee7";
                      }}
                      onBlur={(e) => {
                        e.target.style.border = "1.5px solid #d7dee7";
                        e.target.style.boxShadow = "none";
                      }}
                    >
                      <option value="" disabled>
                        Status
                      </option>
                      <option value="todo">To Do</option>
                      <option value="ongoing">On Going</option>
                      <option value="completed">Completed</option>
                    </select>
                  </div>
                )}
              </div>

              <div className="st">
                <p
                  style={{ width: "40%", color: "#5d7290",  }}
                >
                  Automation Status
                </p>
                {enableEdit ? (
                  <p
                    style={{
                      backgroundColor: "#ebf0f5",
                      fontSize: "0.8rem",
                      padding: "2px 6px",
                      border: "1.5px solid #d7dee7",
                      borderRadius: "4px",
                      color: "#455365",
                    }}
                  >
                    {formatAutomationStatus(testDetail?.automation_status)}
                  </p>
                ) : (
                  <div className="form-group-drop">
                    {/* <label htmlFor="priority" style={{ marginRight: "10px" }}>
                  TestCase Type:
                </label> */}
                    <select
                      id="Automation"
                      name="Automation"
                      value={automationStatus}
                      onChange={handleAutomationStatusChange}
                      style={{
                        outline: "none",
                        border: "1.5px solid #d7dee7",
                        padding: "4px 5px",
                        borderRadius: "4px",
                        transition: "border-color 0.3s, box-shadow 0.3s",
                        fontSize: "0.7rem",
                        color: "#525b6c",
                        fontWeight: "500",
                        backgroundColor: "#ebf0f5",
                      }}
                      onFocus={(e) => {
                        e.target.style.border = "1.5px solid #d7dee7";
                        e.target.style.boxShadow = "0 0 2px #d7dee7";
                      }}
                      onBlur={(e) => {
                        e.target.style.border = "1.5px solid #d7dee7";
                        e.target.style.boxShadow = "none";
                      }}
                    >
                      <option value="" disabled>
                        Automation Status
                      </option>
                      <option value="not-automatable">Not-Automatable</option>
                      <option value="automatabel">Automatable</option>
                      <option value="in-development">In-Development</option>
                      <option value="review">Review</option>
                      <option value="ready">Ready</option>
                    </select>
                  </div>
                )}
              </div>

              <div className="st">
                <p
                  style={{ width: "40%", color: "#5d7290",  }}
                >
                  Priority
                </p>
                {enableEdit ? (
                  <p
                    style={{
                      backgroundColor: "#ebf0f5",
                      fontSize: "0.8rem",
                      padding: "2px 6px",
                      border: "1.5px solid #d7dee7",
                      borderRadius: "4px",
                      color: "#455365",
                    }}
                  >
                    {" "}
                    <MdPriorityHigh
                      style={{
                        color:
                          testDetail?.priority === "class_1"
                            ? "#FF8080"
                            : testDetail?.priority === "class_2"
                            ? "#FABC3F"
                            : testDetail?.priority === "class_3"
                            ? "green"
                            : "black",
                      }}
                    />{" "}
                    {formatClassName(testDetail?.priority)}
                  </p>
                ) : (
                  <div className="form-group-drop">
                    <select
                      id="priority"
                      name="priority"
                      value={Priorities}
                      onChange={handlePriorityChange}
                      style={{
                        outline: "none",
                        border: "1.5px solid #d7dee7",
                        padding: "4px 5px",
                        borderRadius: "4px",
                        transition: "border-color 0.3s, box-shadow 0.3s",
                        fontSize: "0.7rem",
                        color: "#525b6c",
                        fontWeight: "500",
                        backgroundColor: "#ebf0f5",
                      }}
                      onFocus={(e) => {
                        e.target.style.border = "1.5px solid #d7dee7";
                        e.target.style.boxShadow = "0 0 2px #d7dee7";
                      }}
                      onBlur={(e) => {
                        e.target.style.border = "1.5px solid #d7dee7";
                        e.target.style.boxShadow = "none";
                      }}
                    >
                      <option value="" disabled>
                        Priority
                      </option>
                      <option value="class_1">Class 1</option>
                      <option value="class_2">Class 2</option>
                      <option value="class_3">Class 3</option>
                    </select>
                  </div>
                )}
              </div>
            </div>

            <div className="description">
              {/* <p className="name">Description</p>
              <p className="value">
                The purpose of this test case is to rewind VOD content to the
                beginning for PL natco. Precondition * User is logged in. * VOD
                content available to watch.
              </p> */}

              <div className="people">
                <p className="name">People</p>

                <div className="role-section">
                  <div className="stp">
                    <p
                      style={{
                        width: "40%",
                        color: "#6c809b",
                        fontSize: "0.8rem",
                      }}
                    >
                      Asignee
                    </p>
                    <p
                      style={{
                        width: "60%",
                        color: "#425062",
                        fontSize: "0.8rem",
                      }}
                    >
                      <CiUser /> Unassigned
                    </p>
                  </div>

                  <div className="stp">
                    <p
                      style={{
                        width: "40%",
                        color: "#6c809b",
                        fontSize: "0.8rem",
                      }}
                    >
                      Reporter
                    </p>
                    <p
                      style={{
                        width: "60%",
                        color: "#425062",
                        fontSize: "0.8rem",
                      }}
                    >
                      <CiUser /> Nibha
                    </p>
                  </div>
                </div>
              </div>
              <div className="people">
                <p className="name">Dates</p>

                <div className="role-section">
                  <div className="stp">
                    <p
                      style={{
                        width: "40%",
                        color: "#6c809b",
                        fontSize: "0.8rem",
                      }}
                    >
                      Created
                    </p>
                    <p
                      style={{
                        width: "60%",
                        color: "#425062",
                        fontSize: "0.8rem",
                      }}
                    >
                      Updated
                    </p>
                  </div>

                  <div className="stp">
                    <p
                      style={{
                        width: "40%",
                        color: "#6c809b",
                        fontSize: "0.8rem",
                      }}
                    >
                      Reporter
                    </p>
                    <p
                      style={{
                        width: "60%",
                        color: "#425062",
                        fontSize: "0.8rem",
                      }}
                    >
                      Nibha
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="tab-and-outlet">
            <div className="tab_for_test_case">
              <div className="details tab">
                <p>
                  <NavLink
                    className="links"
                    activeClassName="active"
                    to="details"
                  >
                    Details
                  </NavLink>
                </p>
              </div>
              <div className="description_steps tab">
                <p>
                  <NavLink
                    className="links"
                    activeClassName="active"
                    to="description-step"
                  >
                    Test steps
                  </NavLink>
                </p>
              </div>
              <div className="test_group tab">
                <p>
                  <NavLink
                    className="links"
                    activeClassName="active"
                    to="test-group"
                  >
                    Test group
                  </NavLink>
                </p>
              </div>

              <div className="Last_50_run tab">
                <p>
                  <NavLink
                    className="links"
                    activeClassName="active"
                    to="last-run"
                  >
                    Last 50 run
                  </NavLink>
                </p>
              </div>
              <div className="linked_defects tab">
                <p>
                  <NavLink
                    className="links"
                    activeClassName="active"
                    to="defects"
                  >
                    Linked defects
                  </NavLink>
                </p>
              </div>
              <div className="issue tab">
                <p>
                  <NavLink
                    className="links"
                    activeClassName="active"
                    to="issue"
                  >
                    Issue
                  </NavLink>
                </p>
              </div>

              <div className="natco_table tab">
                <p>
                  <NavLink
                    className="links"
                    activeClassName="active"
                    to="natco-table"
                  >
                    Natcos
                  </NavLink>
                </p>
              </div>

              <div className="natco_table tab">
                <p>
                  <NavLink
                    className="links"
                    activeClassName="active"
                    to={`/reports/${testDetail?.jira_id}`}
                  >
                    Reports
                  </NavLink>
                </p>
              </div>
            </div>

            <div className="display">
              <Outlet />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default TestDetails;
